import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import AppsIcon from '@material-ui/icons/Apps';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import GitHubIcon from '@material-ui/icons/GitHub';
// import WebIcon from '@material-ui/icons/Language';
// import WebIcon from '@material-ui/icons/Language';

const useStyles = makeStyles((theme) => ({
  list: {
    minWidth: 250
  },
  menuItem: {
    paddingLeft: (props: MenuItemProps) => theme.spacing(4 * props.level),
  },
  menuSubItem: {
    paddingLeft: (props: MenuItemProps) => theme.spacing(8 * props.level),
  }
}))

export default function ApplicationBar({ onClose = () => { } }) {
  const classes = useStyles();

  return (
    <div className={classes.list}>
      <List subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          @rulyotano
        </ListSubheader>
      }>
        <ListItem button>
          <ListItemIcon>
            <AppsIcon />
          </ListItemIcon>
          <ListItemText primary={"Interesting Projects"} />
        </ListItem>
      </List>
    </div>
  );
}

interface MenuItemProps {
  level: number,
  title: string,
  github?: string,
  web?: string,
  npm?: string,
  nuget?: string,
}

function ProjectMenuItem(props: MenuItemProps) {
  const { title } = props;
  const classes = useStyles(props);
  const [isOpen, setIsOpen] = useState(false);


  return <>
    <ListItem className={classes.menuItem}>
      <ListItemText primary={title} />
      {isOpen ? <ExpandLessIcon /> : <ExpandMoreIcon />}

      <Collapse in={isOpen} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>

          <ListItem button className={classes.menuSubItem}>
            <ListItemIcon>
              <StarBorder />
            </ListItemIcon>
            <ListItemText primary="Starred" />
          </ListItem>

        </List>
      </Collapse>
    </ListItem>
  </>;
}